import React, {useRef} from 'react';
import Slider from "react-slick";

import WebDevelopmentProcessBox from "./WebDevelopmentProcessBox/WebDevelopmentProcessBox";
import SliderDotes from "../../global/SliderDotes/SliderDotes";

import styles from "./WebDevelopmentProcess.module.scss"
import {processBoxesArr} from "../../../assets/UIData";
import {useLazy} from "../../../hooks/useLazy";



function WebDevelopmentProcess({headerImageLoaded}) {
    const sliderRef = useRef()
    const settings = {
        className: `${styles["webDevelopmentProcess__slider"]} sliderDotesDef`,
        autoplay: true,
        autoplaySpeed: 3000,
        dots: true,
        infinite: true,
        slidesToShow: 1,
        slidesToScroll: 1,
        swipeToSlide: true,
        touchThreshold: 1000,
        arrows: false,
        customPaging: i => (<SliderDotes
            onClick={() => sliderRef.current.slickGoTo(i)}/>),
    };
    const {isInView,ref} = useLazy(0.7,headerImageLoaded)
    const webDevelopmentProcessBoxes = processBoxesArr.map((item, index) => (
        <WebDevelopmentProcessBox
            key={index}
            isInView={isInView}
            {...item}
        />
    ))

    return (
        <section className={styles['webDevelopmentProcess']}>
            <h3 className={`${styles["webDevelopmentProcess__title"]} titleDef`}>Our website <span
                className="lightBlueText">development</span> process</h3>
            <div className={styles["webDevelopmentProcess__mainContent"]}>
                <div className={`container`} ref={ref}>
                    <div className={styles["webDevelopmentProcess__deskWrapper"]}>
                        {webDevelopmentProcessBoxes}
                    </div>

                    <Slider {...settings} ref={sliderRef}>
                        {webDevelopmentProcessBoxes}
                    </Slider>

                </div>
            </div>
        </section>
    )
        ;
}

export default WebDevelopmentProcess;